export const y = a => (b => b(b))(b => a(c => b(b)(c)))

export const alertError = a => {
  if (a.response && a.response.data)
    return alert(Object.values(a.response.data).map(b => b.join('\n')).join("\n"));
  else if (a.message)
    return alert(a.message);
  else 
    console.error(a);
}
